import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { Routes, RouterModule } from '@angular/router';

import { AppComponent } from './components/app.component/app.component';
import { HeaderComponent } from './components/header/header.component';
import { MyPassionComponent } from './components/my-passion/my-passion.component';
import { SpotifyIdeasComponent } from './components/spotify-ideas/spotify-ideas.component';
import { AboutMeComponent } from './components/about-me/about-me.component';

const routes: Routes = [
  {path: '', redirectTo: 'aboutme', pathMatch: 'full'}, // was mypassion redirect
  {path: 'aboutme', component: MyPassionComponent}, // was mypassion
  {path: 'services', component: SpotifyIdeasComponent}, // was spotifyideas
  {path: 'gallery', component: AboutMeComponent}, // was aboutme old
  {path: '**', redirectTo: 'aboutme', pathMatch: 'full'} // was redirect to mypassion
];

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    MyPassionComponent,
    SpotifyIdeasComponent,
    AboutMeComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes)
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



